import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ProgramCheckViewModel } from "../../Model/ViewModel/ProgramCheckViewModel";
import { TableViewModel } from "../../Model/ViewModel/TableViewModel";

/**
 * Generated class for the programModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-batch-program-check-add-edit-modal",
  templateUrl: "batch-program-check-add-edit-modal.html"
})
export class BatchProgramCheckAddEditModalPage {
  item: ProgramCheckViewModel;
  temp_table_item:TableViewModel;
  check_table_item:TableViewModel;
  mode: string;
  change_mode:string;
  CanEditBatch: boolean;
  title: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.title = "批次檢核程式";
    this.CanEditBatch = navParams.data.CanEditBatch;
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;

    this.item = new ProgramCheckViewModel();
    if(this.change_mode==='c')
      this.item.program_no="auto";
    else
      this.item.program_no = navParams.data.item.program_no;
    this.item.program_func_desc = navParams.data.item.program_func_desc;
    this.item.temp_schema = navParams.data.item.temp_schema;
    this.item.temp_table = navParams.data.item.temp_table;
    this.item.temp_tablefullname = navParams.data.item.temp_tablefullname;
    this.item.temp_column_check = navParams.data.item.temp_column_check;
    this.item.where_condition = navParams.data.item.where_condition;
    this.item.check_schema = navParams.data.item.check_schema;
    this.item.check_table = navParams.data.item.check_table;
    this.item.check_tablefullname = navParams.data.item.check_tablefullname;
    this.item.check_column = navParams.data.item.check_column;
    this.item.err_key = navParams.data.item.err_key;
    this.item.err_desc = navParams.data.item.err_desc;
    this.item.bus_type_key = navParams.data.item.bus_type_key;
    this.item.bus_type= navParams.data.item.bus_type;
    this.item.prog_type_key = navParams.data.item.prog_type_key;
    this.item.prog_type= navParams.data.item.prog_type;
    this.item.is_active = navParams.data.item.is_active;
    this.item.create_time = navParams.data.item.create_time;
    this.item.creator = navParams.data.item.creator;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.program_type_seq=navParams.data.item.program_type_seq;




  }
  SelectBusType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.bus_type_key,
      code_type: "022"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.bus_type_key = select_data.code_key;
      this.item.bus_type = select_data.code_desc;
    });

    modal.present();
  }
  SelectErrDesc() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.err_key,
      code_type: "003"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.err_key = select_data.code_key;
      this.item.err_desc = select_data.code_desc;
    });

    modal.present();
  }
  SelectProgType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.prog_type_key,
      code_key: "066|02,066|03"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.prog_type_key = select_data.code_key;
      this.item.prog_type = select_data.code_desc;
    });

    modal.present();
  }
  SelectSrcTable() {
    let modal = this.modalCtrl.create("TableSelectModalPage", {
      select_item: this.item.temp_tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.temp_schema = select_data.schemaname;
      this.item.temp_table = select_data.tablename;
      this.item.temp_tablefullname = select_data.tablefullname;
    });

    modal.present();
  }
  SelectTable() {
    let modal = this.modalCtrl.create("TableExtSelectModalPage", {
      select_item: this.item.check_tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.check_schema = select_data.schemaname;
      this.item.check_table = select_data.tablename;
      this.item.check_tablefullname = select_data.tablefullname;
    });

    modal.present();
  }

  SelectSrcColumn() {
    this.temp_table_item = new TableViewModel();
    this.temp_table_item.schemaname= this.item.temp_schema;
    this.temp_table_item.tablename= this.item.temp_table;
    this.temp_table_item.err_columnname= this.item.temp_column_check;
    let modal = this.modalCtrl.create("TableDetailSelectModalPage", {
      table_item:this.temp_table_item,
      // schemaname:this.item.temp_schema,
      // tablename:this.item.temp_table,
      // columnname:this.item.temp_column_check,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.temp_column_check = select_data.columnname;
    });

    modal.present();
  }
  SelectCheckColumn() {

    let modal = this.modalCtrl.create("TableDetailExtSelectModalPage", {
      schemaname:this.item.check_schema,
      tablename:this.item.check_table,
      select_item:this.item.check_column,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.check_column = select_data.columnname;
    });

    modal.present();
  }
  Save() {

    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ProgramModalPage");
  }
}



